<template>
  <div class="container Project_product">
    <div id="loadingDiv" v-show="value">
      <div class="loader"></div>
    </div>

    <div id="myModal" class="modal" v-show="modal">
      <!-- Modal content -->
      <div class="modal-content">
        <div class="justify-content-center">
          <div style="margin-bottom: 0">
            <span v-html="text" class="icon-modal"></span>
            <span style="font-size: 20px">{{ msg }}</span>
            <!-- <div>
              <button class="btn btn-white" v-show="confirm">取消</button>
              <button class="btn btn-success" v-show="confirm">確定</button>
            </div> -->
          </div>
        </div>
      </div>
    </div>

    <div v-if="cart.length === 0">
      購物車內無任何商品
      <a href="/"><i class="fas fa-angle-left mr-2"></i>現在就去逛逛</a>
    </div>
    <div v-else>
      <div class="bar__container">
        <ul class="bar row" id="bar">
          <li class="active">STEP 01 購物車</li>
          <li class="active">STEP 02 填寫資料</li>
          <li>STEP 03 訂購完成</li>
        </ul>
      </div>

      <div class="container form-body mb-4">
        <el-form
          class="form-test mb-5"
          :model="resetPasswordForm"
          :rules="rules"
          ref="resetPasswordForm"
          @submit.prevent.native="submitForm('resetPasswordForm')"
        >
          <el-form-item label="收件人姓名：" prop="name">
            <el-input
              v-model="resetPasswordForm.name"
              placeholder="請輸入真實姓名"
              clearable
              @keypress.enter.native="submitForm('resetPasswordForm')"
            ></el-input>
          </el-form-item>

          <el-form-item label="收件人手機：" prop="tel">
            <el-input
              v-model="resetPasswordForm.tel"
              placeholder="請輸入手機號碼"
              clearable
              @keypress.enter.native="submitForm('resetPasswordForm')"
            ></el-input>
          </el-form-item>

          <el-form-item label="電子信箱：" prop="email">
            <el-input
              v-model="resetPasswordForm.email"
              placeholder="請輸入email"
              clearable
              @keypress.enter.native="submitForm('resetPasswordForm')"
            ></el-input>
          </el-form-item>

          <el-form-item label="寄送地址：" prop="address">
            <el-input
              v-model="resetPasswordForm.address"
              placeholder="請輸入寄送地址"
              clearable
              @keypress.enter.native="submitForm('resetPasswordForm')"
            ></el-input>
          </el-form-item>

          <!-- <el-form-item label="備註">
            <el-input
              type="textarea"
              :rows="3"
              v-model="resetPasswordForm.desc"
              placeholder="備註的事項留言在這裡~"
            ></el-input>
          </el-form-item> -->

          <el-form-item style="text-align: right">
            <router-link to="/cart" class="text-dark">
              <button class="btn cart-back-btn">
                <i class="fas fa-angle-left mr-2"></i>回購物車
              </button></router-link
            >
            <button
              class="btn btn-success cart-btn ml-2"
              ref="btnSubmitForm"
              native-type="submit"
            >
              提交訂單
            </button>
          </el-form-item>
        </el-form>
      </div>
    </div>
  </div>
</template>

<script>
// import $ from "jquery"
import { isTel, isEmail } from "./validate.js";

export default {
  name: "MyCart",
  data() {
    return {
      msg: "",
      text: "",
      modal: false,
      confirm: false,
      cart: [],
      value: true,
      resetPasswordForm: {
        name: "",
        email: "",
        tel: "",
        address: "",
        payment: "",
        coupon: "",
        message: ""
      },
      rules: {
        name: [
          { required: true, message: "收件人姓名為必填", trigger: "blur" },
        ],
        tel: [
          { required: true, message: "收件人手機為必填", trigger: "blur" },
          {
            type: "string",
            min: 10,
            message: "手機號碼不得大於10個字元",
            trigger: "blur",
          },
          { validator: isTel, trigger: "blur" },
        ],
        email: [
          { required: true, message: "電子信箱為必填", trigger: "blur" },
          { validator: isEmail, trigger: "blur" },
        ],
        address: [{ required: true, message: "寄送地址必填", trigger: "blur" }],
      },
    };
  },
  created() {
    this.getCart();
  },
  methods: {
    getCart() {
      const api =
        "https://course-ec-api.hexschool.io/api/d25970ec-b87b-44f6-9785-9bdbbcd8dc56/ec/shopping";
      this.$http.get(api).then((res) => {
        this.cart = res.data.data;
        console.log(this.cart);
        this.value = false;
      });
    },
    submitForm(form) {
      this.$refs[form].validate((valid) => {
        if (valid) {
          // console.log(valid);
          // console.log("submit!");

          const order = { ...this.resetPasswordForm };

          const api =
            "https://course-ec-api.hexschool.io/api/d25970ec-b87b-44f6-9785-9bdbbcd8dc56/ec/orders";

          this.$http
            .post(api, order)
            .then(() => {
              this.$bus.$emit("updateCart");
              this.$router.push("/check");
              console.log(order);
              // this.$bus.$emit("updateCart");
              // this.$router.push("/checkout");
              // this.isLoading = false;
            })
            .catch(() => {
              console.log("訂單失敗");
            });
        } else {
          console.log("error submit!!");
          this.text =
            '<i class="fa-solid fa-triangle-exclamation" style="color: red;"></i>';
          this.msg = "資料尚未填寫完成";
          this.modal = true;
          this.modal = setTimeout(() => {
            this.modal = false;
          }, 1000 * 1 * 1);
          return false;
        }
      });
    },
  },
};
</script>

<style>
@import url(https://fonts.googleapis.com/css?family=Montserrat);
* {
  box-sizing: border-box;
}

label {
  margin-bottom: 0 !important;
}

.cart-back-btn {
  border: 1px solid #218838;
  background-color: #fff;
  color: #218838;
  width: 180px !important;
  cursor: pointer;
}

.cart-btn {
  width: 180px !important;
  cursor: pointer;
}

.title {
  margin-bottom: 30px;
  text-align: center;
  color: #020304;
}

.bar__container {
  width: 100%;
  /* max-width: 600px;
  margin: 0 auto; */
}

.bar {
  counter-reset: step;
  margin: 0;
  padding: 0;
}

.bar li {
  list-style-type: none;
  /* float: inherit; */
  width: 32%;
  /* position: relative; */
  text-align: center;
  font-size: 9px;
  /* color: white; */
  /* padding-left: 5px; */
  background-color: #fff;
  border: 1px solid #dfdfdf;
  border-top-right-radius: 30px;
  border-bottom-right-radius: 30px;
  /* border-radius: 17px; */
  padding: 10px;
}

.bar li.active {
  background-color: #28a745;
  /* border-radius: 41px; */
  color: #ffffff;
  /* border-image: stretch; */
  border-top-right-radius: 30px;
  border-bottom-right-radius: 30px;
}
@media all and (min-width: 500px) {
  .bar li {
    text-transform: uppercase;
    font-size: 17px;
  }
}
.bar li:before {
  /* content: counter(step); */
  /* counter-increment: step; */
  content: "";
}
/* .bar li:after {
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  background: #ddd;
  top: 15px;
  left: -50%;
  z-index: -1;
} */
.bar li:first-child:after {
  content: none;
}
/* .bar li.active:before {
  background: #27ae60;
  border: 1px solid #89e5b0;
  color: white;
} */
.bar li.active + li.active:after {
  background: #27ae60;
}
.bar li.active:first-child + li:after:not(.active) {
  background: white;
}

.form-body {
  display: inline-block;
  margin-top: 4rem;
}

.form-test {
  background-color: #fff;
  padding: 2rem;
  box-shadow: 2px 2px 14px 1px rgb(128 128 128 / 30%);
  border-radius: 4px;
  margin: 0 auto;
}

@media (min-width: 992px) {
  .form-test {
    width: 800px;
  }
}

.el-form-item__label {
  font-size: 15px !important;
}

.el-input__inner::placeholder {
  color: #8b8b8b;
}
/* google */
.el-input__inner::-webkit-input-placeholder {
  color: #8b8b8b;
}
/* firefox */
.el-input__inner:-moz-placeholder {
  color: #8b8b8b;
}
/*ie*/
.el-input__inner:-ms-input-placeholder {
  color: #8b8b8b;
}

.modal {
  display: block;
  position: fixed; /* Stay in place */
  z-index: 1; /* Sit on top */
  left: 0;
  top: 0;
  width: 100%; /* Full width */
  height: 100%; /* Full height */
  overflow: auto; /* Enable scroll if needed */
  background-color: rgba(0, 0, 0, 0.4); /* Black w/ opacity */
}

/* Modal Content/Box */
.modal-content {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  background-color: #fefefe;
  /* margin: 15% auto; */
  padding: 20px;
  border: 1px solid #888;
  max-width: 100%;
}

.icon-modal {
  font-size: 25px;
  vertical-align: middle;
  margin-right: 5px;
}
</style>