/** 這個文件只允許放表單驗證方法 **/
//驗證手機號碼
export function isTel(rule, value, callback) {
    var phone_rule = /^[0-9]{9,10}$/
    if (phone_rule.test(value)) {
        return callback()
    }
    return callback(new Error('輸入的手機號碼錯誤'))
}

//驗證電子信箱
export function isEmail(rule, value, callback) {
    var email_rule = /^[a-z0-9](?:[-_.+]?[a-z0-9]+)*@[A-Za-z0-9]+((\.|-)[A-Za-z0-9]+)*\.[A-Za-z]+$/
    // var email_rule = /^\w+((-\w+)|(\.\w+))*'\@[A-Za-z0-9]+((\.|-)[A-Za-z0-9]+)*\.[A-Za-z]+$/
    if (email_rule.test(value)) {
        return callback()
    }
    return callback(new Error('電子信箱格式錯誤'))
}